<template>
  <div @touchmove="windowScroll" @mousewheel="windowScroll">
    <a href="#" class="backTop" v-show="aShow" @click="removea">回到顶部</a>
    <!-- 搜索框 -->
    <div class="search">
      <router-link to="/search">
        <div class="two">
          <div class="tu">
            <svg
              t="1665993997414"
              class="icon"
              viewBox="0 0 1024 1024"
              version="1.1"
              xmlns="http://www.w3.org/2000/svg"
              p-id="2546"
              width="200"
              height="200"
            >
              <path
                d="M704 627.2h-40.8l-14.4-14.4c50.4-58.4 80-133.6 80-216C728.8 212.8 580 64 396 64S64.8 213.6 64.8 396.8s148.8 332.8 332.8 332.8c82.4 0 158.4-30.4 216-80l14.4 14.4v40l256 255.2 76-76c-0.8 0-256-256-256-256z m-307.2 0c-127.2 0-230.4-103.2-230.4-230.4s103.2-230.4 230.4-230.4 230.4 103.2 230.4 230.4-103.2 230.4-230.4 230.4z"
                p-id="2547"
                fill="#ffffff"
              ></path>
            </svg>
          </div>
          <div class="text">搜索</div>
        </div>
      </router-link>
    </div>
    <!-- tab list 标签栏 -->
    <div class="tag">
      <van-tabs
        swipeable
        color="rgb(7,193,96)"
        border
        @change="onClick"
        title-active-color="black"
      >
        <van-tab
          v-for="item in list"
          :name="item._id"
          :title="item.name"
          :key="item._id"
        >
          <van-pull-refresh
            v-model="refreshing"
            success-text="刷新成功"
            @refresh="onRefresh"
          >
            <van-list
              v-model="loading"
              :finished="finished"
              finished-text="没有更多了"
              @load="onLoad(item._id)"
              class="cells"
            >
              <van-cell
                v-for="(item, index) in wzlist"
                :key="index"
                @click="xq(item._id)"
              >
                <div class="listbox">
                  <div class="nr" v-if="item.imageSrc.length === 1">
                    <div class="listtitle">{{ item.title }}</div>
                    <div class="listimg">
                      <img :src="item.imageSrc[0]" @error="imgerror" alt="" />
                    </div>
                  </div>
                  <div class="nrs" v-else-if="item.imageSrc.length > 1">
                    <div class="listtitle" style="width: 360px">
                      {{ item.title }}
                    </div>
                    <div class="listimgs">
                      <div v-for="(i, index) in item.imageSrc" :key="index">
                        <img :src="i" alt="" @error="imgerror" />
                      </div>
                    </div>
                  </div>
                  <div class="nrss" v-else>
                    <div class="listtitle">{{ item.title }}</div>
                  </div>
                  <div class="bigfoot">
                    <div class="listfoot">
                      <div>作者:{{ item.author }}</div>
                      <div>{{ item.comment }}评论</div>
                      <div>发布于:{{ ontime(item.time) }}</div>
                    </div>
                    <div class="close"></div>
                  </div>
                </div>
              </van-cell>
            </van-list>
          </van-pull-refresh>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { Tab, Tabs, List, PullRefresh, Cell, Toast } from "vant";
export default {
  name: "index",
  data() {
    return {
      value: "",
      list: [],
      wzlist: [],
      loading: false,
      finished: false,
      refreshing: false,
      num: 10,
      aShow: false,
      articleid: "",
    };
  },
  components: {
    [PullRefresh.name]: PullRefresh,
    [Tab.name]: Tab,
    [Tabs.name]: Tabs,
    [Cell.name]: Cell,
    [List.name]: List,
  },
  methods: {
    windowScroll(e) {
      // console.log(e.view.scrollY);
      // console.log(e);
      if (e.view.scrollY >= 1500) {
        this.aShow = true;
        // console.log(1);
      } else {
        this.aShow = false;
      }
    },
    removea() {
      this.aShow = false;
    },
    onClick(name, title) {
      this.articleid = name;
      // console.log(name);
      //  获取文章列表
      axios({
        method: "post",
        url: `https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_article_list`,
        data: {
          cate_id: name, //分类ID
          skip: 0,
          limit: 10,
        },
      }).then((res) => {
        // console.log(res.data.data);
        this.wzlist = res.data.data;
      });
    },
    xq(id) {
      // console.log(id);
      this.$router.push({
        name: "lcontent",
        query: {
          id,
        },
      });
    },
    onLoad(id) {
      axios({
        method: "post",
        url: `https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_article_list`,
        data: {
          cate_id: id ? id : "609e99e0f2e56f000175151f", //分类ID
          skip: this.wzlist.length,
          limit: 10,
        },
      }).then((res) => {
        // console.log(res.data.data);
        // console.log(this.wzlist.length);
        this.wzlist.push(...res.data.data);
        this.loading = false;
        this.refreshing = false;
        if (this.wzlist.length >= res.data.count) {
          this.finished = true;
        }
      });
    },
    onRefresh() {
      this.wzlist = [];
      this.onLoad(this.articleid);
      // console.log(this.articleid);
      this.finished = false;
    },
  },
  created() {
    axios({
      method: "get",
      url: "https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_cate_list",
    }).then((res) => {
      this.articleid = res.data.data[0]._id;
      this.list = res.data.data;
    });
  },
  activated() {
    this.aShow = false;
  },

  beforeRouteEnter(to, from, next) {
    if (from.path == "/content") {
      next((vm) => {
        setTimeout(() => {
          if(window.scrollY>1500) {
            vm.aShow = true;
          }else {
            vm.aShow = false;
          }
        }, 200);
      });
    }
    next();
  },
};
</script>
