import { Toast } from 'vant';
import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path:'/',
    redirect:'index'
  },
  {
    path: '/index',
    name: 'index',
    component: () => import('../views/index.vue'),
    meta:{
      root:true
    }
  },
  {
    path: '/login',
    name: 'login',
    component: () => import('../views/login.vue')
  },
  {
    path: '/register',
    name: 'register',
    component: () => import('@/views/register.vue')
  },
  {
    path: '/public',
    name: 'public',
    component: () => import('../views/public.vue'),
    meta:{
      root:true
    }
  },
  {
    path: '/my',
    name: 'my',
    component: () => import('../views/my.vue'),
    meta:{
      root:true
    }
  },
  {
    path: '/search',
    name: 'search',
    component: () => import('@/components/search.vue')
  },
  {
    path: '/information',
    name: 'information',
    component: () => import('@/components/information.vue')
  },
  {
    path: '/content',
    name: 'lcontent',
    component: () => import('@/components/content.vue')
  },
  {
    path: '/history',
    name: 'history',
    component: () => import('@/components/history.vue')
  },
  {
    path: '/seehistory',
    name: 'seehistory',
    component: () => import('@/components/seehistory.vue')
  },
  {
    path: '/collection',
    name: 'collection',
    component: () => import('@/components/collection.vue')
  },
  {
    path: '/forgotPassword',
    name: 'forgotPassword',
    component: () => import('@/components/forgotPassword.vue')
  }
]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
});

router.beforeEach((to,from,next) =>{
  if((to.name == 'history' || to.name =='seehistory' || to.name =='collection' || to.name=='information') && !localStorage.getItem('uid')){
    Toast('请先登录')
    next('/login')
  }else{
    next()
  }
  // console.log(from.name);
  // console.log(to.name);
  // console.log('----------------------------------------------');
  
})

export default router
